@import url(https://fonts.googleapis.com/css?family=Work+Sans);
body{margin:0;font-family:'Work Sans';-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#eceff1;color:#4d4d4d}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.noselect{-webkit-touch-callout:none;-webkit-user-select:none;-ms-user-select:none;user-select:none}

.App{margin-top:0.5rem}

.Pokedex{text-align:center}.Pokedex .Pokedex-title{margin:0}.Pokedex .Pokedex-winner{color:#4caf50}.Pokedex .Pokedex-loser{color:#e91e63}.Pokedex .Pokedex-sub-title{margin:0}.Pokedex .Pokedex-cards{display:flex;justify-content:center;flex-wrap:wrap}

.Pokecard{width:300px;padding:10px;margin:1rem;box-shadow:7px 10px 12px -5px rgba(0,0,0,0.56);text-align:center;background-color:white;border-radius:10px}.Pokecard .Pokecard-title{margin-bottom:1rem;font-weight:bold;font-size:1.5rem}.Pokecard .Pokecard-image{display:flex;justify-content:center;align-items:center;height:250px;background-color:#eceff1;border-radius:10px;margin-bottom:1rem}.Pokecard .Pokecard-image img{transition:all ease 500ms}.Pokecard .Pokecard-image img:hover{-webkit-transform:scale(1.25);transform:scale(1.25);opacity:0.8}.Pokecard .Pokecard-data{font-size:80%}

